@import "styles";

@import "mixins";

#links {
  list-style: none;
  padding: 0;
  overflow: auto;
  float: left;
  margin-left: 60px;
  margin-top: 20px;
}

#links li {
  display: inline;
  float: left;
}

#links li a {
  float: left;
  display: block;
  padding: 8px 20px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 55px;
  font-family: $breadfont;
  font-size: 15px;
}

#links li a:hover {
  color: #6a6a4c;
}

#book-link {
  list-style: none;
  padding: 0;
  overflow: auto;
  float: right;
  margin-right: 35px;
  margin-top: 15px;
}

#book-link li {
  display: inline;
  float: right;
  background-color: #918568;
  width: 180px;
  height: 50px;
}

#book-link li a {
  float: right;
  display: block;
  padding: 8px 20px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 55px;
  font-family: $breadfont;
  font-size: 18px;
  margin-right: 45px;
  margin-top: 6px;
}

#book-link:hover {
  background-color: red;
}

#contact-link li {
  display: inline;
  float: right;
  border: 1px solid #918568;
  width: 180px;
  height: 48px;
}

#contact-link {
  list-style: none;
  padding: 0;
  overflow: auto;
  float: right;
  margin-right: 20px;
  margin-top: 15px;
}

#contact-link li a {
  float: right;
  display: block;
  padding: 8px 20px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 55px;
  font-family: $breadfont;
  font-size: 18px;
  margin-right: 28px;
  margin-top: 6px;
}

#contact-link:hover {
  background-color: #797e59;
}

#header {
  background-color: #303223;
  margin: 0;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
}

h4 {
