.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: #000;
  padding: 0px 16px;
  background: #303223;
}

.dropdown:hover .dropbtn {
  background-color: #303223;
}

.dropdown-content {
  margin-left: 0;
  overflow: hidden;
  display: none;
  position: absolute;
  background-color: #303223;
  width: 185px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.5);
  z-index: 1;
  top: 56px;
}

.dropdown-content a {
  float: none;
  color: black;
  text-decoration: none;
  display: block;
  text-align: left;
  width: 185px;
  background: #303223;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

#quote p {
  font-family: $breadfont;
  margin-top: 330px;
}

